// Home page about us image to left
const aboutImage = document.querySelector("section.about-us .image-wrapper")
const sectorImage = document.querySelector("section.sector-detail .image-wrapper")

function imageToLeft(image) {
    if (window.innerWidth > mobileLimit) {
        const imageLeft = image.getBoundingClientRect().left
        image.style.transform = `translateX(-${imageLeft}px)`;
    }
}

function sectorImageToLeft(image) {
    if (window.innerWidth > mobileLimit) {
        const imageLeft = image.getBoundingClientRect().left
        image.style.transform = `translateX(-${imageLeft}px)`;
        image.style.width = `${image.offsetWidth + imageLeft - 50}px`;
    }
}

window.addEventListener("load", () => {
    if (aboutImage) imageToLeft(aboutImage);
    if (sectorImage) sectorImageToLeft(sectorImage);
})


function writeSlideNamesToPagination(sliders, items) {
    sliders.forEach((slide, index) => {
        let slideName = slide.getAttribute("data-slide-name");
        let paginationItem = items[index];
        const paginateItem = paginationItem.querySelector("span");
        paginateItem.innerHTML = slideName;
    });
}

window.addEventListener("load", () => {
    // Write home page main slide names to pagination
    let sliders = document.querySelectorAll(".main-slider .swiper-slide");
    let paginationItems = document.querySelectorAll(".main-slider .swiper-pagination-bullet");
    writeSlideNamesToPagination(sliders, paginationItems)

    // Write home page sector slide names to pagination
    let sectorSliders = document.querySelectorAll(".sector-slider .swiper-slide");
    let sectorPaginationItems = document.querySelectorAll(".sector-pagination .swiper-pagination-bullet");
    writeSlideNamesToPagination(sectorSliders, sectorPaginationItems)
});


// News page custom dropdown
const dropdown = document.querySelectorAll(".dropdown");

if (dropdown) {
    dropdown.forEach(item => {
        const dropdownPlaceholder = item.querySelector(".placeholder"),
            dropdownList = item.querySelector(".dropdown-list"),
            listHeight = dropdownList.offsetHeight;

        dropdownList.style.top = dropdownPlaceholder.offsetHeight + 15 + "px";
        body.addEventListener("click", (e) => {
            const isItem = e.target === item || item.contains(e.target);

            if (isItem) {
                item.classList.toggle("active");
                dropdownList.style.height = listHeight + "px";
            } else if (item.classList.contains("active")) {
                // Tıkladığımız alan dropdown içerisinde değilse dropdown
                // kapansın
                item.classList.remove("active");
                dropdownList.style.height = 0;
            }
        })
    })
}


