// Corporate tab area
const boxes = document.querySelector(".box-area");

if (boxes) {
    const box = boxes.querySelectorAll(".tab"),
        boxContent = boxes.querySelectorAll(".content"),
        boxImage = document.getElementById("box-image");

    function corporateBoxEvent(ev, tabID) {
        let i;

        for (i = 0; i < box.length; i++) {
            box[i].classList.remove("active")
        }

        for (i = 0; i < boxContent.length; i++) {
            boxContent[i].classList.remove("active")
        }

        ev.currentTarget.classList.add("active")
        document.getElementById(tabID).classList.add("active");
    }

    box.forEach(tab => {
        tab.addEventListener("click", (e) => {
            boxContent.forEach(item => {
                const tabID = tab.getAttribute("data-tab-id");
                const contentID = item.id;
                const tabImage = tab.getAttribute("data-image");
                if (tabID === contentID) {
                    corporateBoxEvent(e, `${contentID}`);

                    if (windowWidth > mobileLimit) {
                        boxImage.classList.add("changed");
                        setTimeout(() => {
                            boxImage.classList.remove("changed");
                            boxImage.src = tabImage;
                        }, 500)
                    }
                    //
                    // if (window.innerWidth < mobileLimit) {
                    //     document.querySelector(".boxes-content").scrollIntoView({
                    //         behavior: "smooth"
                    //     })
                    // }
                }
            })
        })
    })
}


// Corporate page policy area tab
const policies = document.querySelector(".policies")
window.addEventListener("DOMContentLoaded", () => {
    // Header js içerisinde sub menu item'in click durumunda session storage oluşturdum
    let data = parseInt(sessionStorage.getItem("accordion"))
    sessionStorage.removeItem("accordion")
    sessionStorage.clear()

    if (policies) {
    const tab = policies.querySelectorAll(".tabs .tab"),
        policyTabImage = document.getElementById("tab-image");

    function corporatePolicyEvent(ev, desc, descHeight) {
        let i;

        for (i = 0; i < tab.length; i++) {
            tab[i].classList.remove("active")
            tab[i].querySelector(".description").style.height = "0px"
        }

        ev.currentTarget.classList.add("active")
        desc.style.height = `${descHeight}px`

        // if (window.innerWidth < mobileLimit) {
        //     ev.currentTarget.scrollIntoView({
        //         behavior: "smooth"
        //     })
        // }
    }

    tab.forEach((tab, index) => {
        const desc = tab.querySelector(".description"),
            descHeight = desc.offsetHeight,
            tabImage = tab.getAttribute("data-image");
        desc.style.height = "0px"

        // Eğer kurumsal menüsü içerisinden alt menüdeki politikalardan herhangi birine tıklanırsa kurumsal sayfası açıldığında o accordion açılsın
        if (data) {
            tab.classList.remove("active")
            if (index === data) {
                tab.querySelector(".description").style.height = `${descHeight}px`
                tab.classList.add("active")
                policyTabImage.src = tab.getAttribute("data-image")
            }
            // Eğer kurumsal menüsüne alt menü itemleri yoluyla girilmemişse yani data yoksa ise ilk accordion işlemleri gerçekleşsin
        } else if (!data && index === 0) {
            desc.style.height = `${descHeight}px`
            policyTabImage.src = tabImage
        }

        // Eğer kurumsal sayfası içerisinde mega menü içerisinden herhangi bir politikaya tıklanırsa sayfa içinde o accordion açılsın
        const subMenuItem = header.querySelectorAll(".submenu-item");
        subMenuItem.forEach((item, key) => { item.addEventListener("click", () => {
            let policyTab = parseInt(item.getAttribute("data-tab-id"))
            desc.style.height = "0px"
            tab.classList.remove("active")
            if (index === policyTab) {
                tab.classList.add("active")
                tab.querySelector(".description").style.height = `${descHeight}px`
                policyTabImage.src = tab.getAttribute("data-image")
            }
        }) })

        tab.addEventListener("click", (e) => {
            corporatePolicyEvent(e, desc, descHeight);
            policyTabImage.parentElement.classList.add("changed");
            setTimeout(() => {
                policyTabImage.parentElement.classList.remove("changed");
                policyTabImage.src = tabImage;
            }, 500)
        })
    })
}
})



