// Corporate page fixed bar
const fixedBar = document.querySelector(".fixed-bar");

if (fixedBar) {
    const items = fixedBar.querySelectorAll(".item");

    // Corporate page section görünür olduğunda fixed olan barda ilgili item aktif olacak.
    const calcRect = (section, item, itemID, sectionID) => {
        const rect = section.getBoundingClientRect();

        const sectionVisible = rect.top <= section.clientHeight / 2
        const isBelowTop = rect.top >= -section.clientHeight + fixedBar.offsetHeight;
        if (sectionVisible) {
            section.classList.add("active")

            if (itemID === sectionID) {
                item.classList.add("active")
            } else {
                item.classList.remove("active")
            }
        }

        if (!isBelowTop) {
            section.classList.add("is-below-top")
            section.classList.remove("active")
        }
    };

    function removeActive(ev) {
        for (let i = 0; i < items.length; i++) {
            items[i].classList.remove("active")
        }

        ev.currentTarget.classList.add("active")
    }

    items.forEach(item => {
        item.addEventListener("click", (e) => {
            removeActive(e)
        })
    })

    window.addEventListener("scroll", () => {
        items.forEach(item => {
            const itemID = item.getAttribute("data-section-id"),
                sections = document.querySelectorAll("section.content > section");
            sections.forEach(section => {
                const sectionID = section.getAttribute("data-section-id")
                calcRect(section, item, itemID, sectionID)
            })
        })
    })

    const breadcrumb = document.getElementById("breadcrumb");
    window.addEventListener("scroll", () => {
        if (window.innerWidth > mobileLimit) {
            if (window.scrollY > fixedBar.getBoundingClientRect().top + breadcrumb.offsetHeight) {
                fixedBar.classList.add("is-fixed")
                header.classList.add("is-hidden")
            } else {
                fixedBar.classList.remove("is-fixed")
                header.classList.remove("is-hidden")
            }

            if (header.classList.contains("is-fixed") && !header.classList.contains("is-hidden")) {
                fixedBar.classList.add("extra-top")
            } else {
                fixedBar.classList.remove("extra-top")
            }
        }
    })
}


