// GLOBAL VARIABLES
// ------------------------------------------------------------
window.body = document.querySelector('body');
window.header = document.querySelector('header');
window.footer = document.querySelector('footer');
window.main = document.querySelector('main');
window.windowWidth = window.innerWidth;
window.windowHeight = window.innerHeight;
window.mobileLimit = 1024;

// LIBRARIES
// ------------------------------------------------------------
// FancyBox ---------------------------------------------------------
import { Fancybox } from '@fancyapps/ui'
window.Fancybox = Fancybox

// FancyBox Settings
Fancybox.defaults.placeFocusBack = 0;
Fancybox.defaults.autoFocus = false;
Fancybox.defaults.trapFocus = false;
Fancybox.defaults.dragToClose = false;

Fancybox.bind("[data-fancybox]", {
    Toolbar: {
        display: {
            left: ["infobar"],
            middle: [],
            right: ["thumbs", "close"],
        },
    },
});

Fancybox.bind("[data-offer-fancybox]", {
    // Toolbar: {
    //     display: {
    //         left: ["infobar"],
    //         middle: [],
    //         right: ["thumbs", "close"],
    //     },
    // },

    closeButton:
        '<button data-fancybox-close class="f-button is-close-btn" title="{{CLOSE}}"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" tabindex="-1"><path d="M20 20L4 4m16 0L4 20"/></svg></button>',

    groupAttr: "data-offer-fancybox",
    mainClass: "offer-fancybox"
});

