// GSAP
// ------------------------------------------------------------
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger'
import DrawSVGPlugin from 'gsap/DrawSVGPlugin'
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ScrollReveal from "scrollreveal";

gsap.registerPlugin(ScrollTrigger, DrawSVGPlugin, ScrollToPlugin);

window.ScrollTrigger = ScrollTrigger;
window.DrawSVGPlugin = DrawSVGPlugin;
window.ScrollToPlugin = ScrollToPlugin;
window.gsap = gsap;

window.addEventListener('load', function () { ScrollTrigger.refresh(true); }, false);

// DEFAULT | GSAP SCRIPTS
// ------------------------------------------------------------

// SCROLLABLE SELECTOR CLICK
// TIKLAYINCA data-scrollable-section ALANINA SCROLL EDER
// ------------------------------------------------------------
if (document.querySelector('.scrollable-selector')) {
    document.querySelectorAll(".scrollable-selector").forEach((element, index) => {
        element.addEventListener("click", () => {
            let scrollableSection = element.getAttribute('data-scrollable-section');

            // Corporate sayfasında fixed bar için
            if (document.querySelector(".fixed-bar") && windowWidth < mobileLimit) {
                gsap.to(window, { duration: 1, scrollTo: { y: scrollableSection, offsetY: 50 } });
                console.log("offset 50 mobil");
            } else if (document.querySelector(".fixed-bar")) {
                gsap.to(window, { duration: 1, scrollTo: { y: scrollableSection, offsetY: 200 } });
                console.log("offset 200");
            } else if (element.classList.contains("scroll-area")) {
                gsap.to(window, { duration: 1, scrollTo: { y: scrollableSection, offsetY: 100 } });
            } else {
                gsap.to(window, { duration: .65, scrollTo: { y: scrollableSection, offsetY: 10 } });
                console.log("offset 10")
            }
        });
    });
}

// PROJECT BASED |  GSAP SCRIPTS

if (window.innerWidth > mobileLimit) {
    ScrollReveal().reveal('.reveal', {
        delay: 400,
        duration: 650,
        reset: false
    });
}

const paths = document.querySelectorAll('header svg > path');
const linearPaths = document.querySelectorAll('header svg g');
const whitePaths = document.querySelectorAll('#breadcrumb svg path');

if (paths) {
    gsap.from(paths, {
        duration: 1,
        opacity: 0,
        stagger: 0.2,
        ease: 'power2.out',
    });
}

if (linearPaths) {
    gsap.from(linearPaths, {
        duration: 1,
        delay: 1.5,
        y: 10,
        x: 5,
        opacity: 0,
        stagger: 0.2,
        ease: 'power3.out'
    });
}

if (whitePaths) {
    gsap.from(whitePaths, {
        duration: 0.3,
        y: 50,
        x: 20,
        opacity: 0,
        stagger: 0.1,
        ease: 'power2.in'
    });
}

const scrollTopButton = document.getElementById("scroll-top")

scrollTopButton.addEventListener("click", () => {
    gsap.to(window, {
        scrollTo: 0,
        duration: 1.5
    })
})

