// SMART MENU
let headerHeight = document.querySelector('.header-field').offsetHeight;
let currentScrollTop = window.pageYOffset || document.documentElement.scrollTop;
if (currentScrollTop >= (headerHeight / 2)) { header.classList.add('is-hidden'); header.classList.add('is-fixed'); }
else { header.classList.remove('is-hidden'); header.classList.remove('is-fixed'); }

let lastScrollTop = 0;
window.addEventListener('scroll', function () {
    let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    if (scrollTop > lastScrollTop && scrollTop > (headerHeight / 2)) {
        header.classList.add('is-hidden'); header.classList.add('is-fixed');
        if (document.querySelector('.header-field').classList.contains('is-white')) document.querySelector('.header-field').classList.add('is-colored');
    }
    else {
        if (scrollTop > (headerHeight / 2)) { header.classList.remove('is-hidden'); header.classList.add('is-fixed'); }
        else {
            header.classList.remove('is-hidden'); header.classList.remove('is-fixed');
            if (document.querySelector('.header-field').classList.contains('is-white')) document.querySelector('.header-field').classList.remove('is-colored');
        }
    }
    lastScrollTop = scrollTop;
}, false);

// HEADER SPACE FIX
let headerSpacePadding = document.querySelectorAll('.header-space-padding');
if (headerSpacePadding.length) {
    headerSpacePadding.forEach(element => {
        element.style.paddingTop = `${document.querySelector('header').offsetHeight}px`;
    });
}
let headerSpaceMargin = document.querySelectorAll('.header-space');
if (headerSpaceMargin.length) {
    headerSpaceMargin.forEach(element => {
        element.style.marginTop = `${document.querySelector('header').offsetHeight}px`;
    });
}

// DYNAMIC STICKY
let dynamicSticky = document.querySelectorAll('.dynamic-sticky');
if (dynamicSticky) {
    dynamicSticky.forEach(element => {
        let headerHeight = document.querySelector('.header-field').offsetHeight; // HEADER HEIGHT
        // MOUSE YUKARI SCROLL
        let lastScrollTop = 0;
        window.addEventListener('scroll', function () {
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            if (scrollTop > lastScrollTop) {
                element.classList.add('is-sticky', 'top-60');
            }
            else {
                element.classList.remove('is-sticky', 'top-60');
            }
            lastScrollTop = scrollTop;
        }, false);

        // MOUSE AŞAĞI SCROLL
        let lastScrollBottom = 0;
        window.addEventListener('scroll', function () {
            let scrollBottom = window.pageYOffset || document.documentElement.scrollTop;
            if (scrollBottom > lastScrollBottom) {
                element.classList.remove('is-sticky-bottom');
                element.style.top = '60px'
            }
            else {
                element.classList.add('is-sticky-bottom');
                element.style.top = `${headerHeight + 30}px`;
            }
            lastScrollBottom = scrollBottom;
        }, false);
    });
}

// COOKIE
let cookie = document.querySelector('.cookie-box');
let cookieClose = document.querySelectorAll('.close-cookie');
let cookieAccept = document.querySelectorAll('.accept-cookie');

// COOKIE FIELD
(Cookies.get('cookie') && Cookies.get('cookie') == 'accepted') ? cookie.classList.add('accepted') : cookie.classList.remove('translate-y-[150%]');

// COOKIE CLOSE
cookieClose.forEach(close => {
    close.addEventListener('click', () => {
        cookie.classList.add('!translate-y-[125%]');
    });
});

// COOKIE ACCEPT
cookieAccept.forEach(element => {
    element.addEventListener('click', () => {
        cookie.classList.add('translate-y-[150%]');
        Cookies.set('cookie', 'accepted', { expires: 365 });
    }, false);
});

