let langSwitcher = document.getElementById("lang-switcher");
let headerNav = document.getElementById("header-nav"),
    menuBtn = document.getElementById("nav-menu-button"),
    menuItem = header.querySelectorAll(".menu-item"),
    megaMenu = header.querySelectorAll(".mega-menu")
body.addEventListener("click", (e) => {
    if (window.innerWidth <= mobileLimit) {
        const isLangSwitcher = e.target === langSwitcher || langSwitcher.contains(e.target);

        if (isLangSwitcher) {
            langSwitcher.classList.toggle("active");
            menuBtn.classList.remove("active");
            headerNav.classList.remove("active");
            header.classList.remove("mobile-menu-active");
            body.classList.remove("overflow-hidden");
        } else if (langSwitcher.classList.contains("active")) {
            // Tıkladığımız alan dil seçici içerisinde değilse dil seçici kapansın
            langSwitcher.classList.remove("active");
        }
    }
})

const addIconOnMobile = (icon, mainItem) => {
    // automatically add dropdown icon when item class list contains has-sub-menu
    if (mainItem.classList.contains("has-sub-menu")) {
        icon.className = "icon-angle-down";
        mainItem.insertAdjacentElement("beforeend", icon);
    } else {
        icon.remove();
    }
}

let closeMega = (index, ev, subMenu, height) => {
    let i, mega, item, x
    for (i = 0; i <= menuItem.length; i++) {
        mega = megaMenu[i]
        item = menuItem[i]
        if (mega && item) {
            if (i !== index) {
                // menuItem[i+1].classList.remove("dropdown-active")
                item.classList.remove("dropdown-active")
                mega.style.height = `0px`;
            }
            if (i === index) {
                menuItem[index+=2].classList.remove("dropdown-active")
            }

        }
    }

    ev.currentTarget.classList.toggle("dropdown-active");
    subMenu.style.height = `${height}px`;
    if (!ev.currentTarget.classList.contains("dropdown-active")) {
        subMenu.style.height = `0px`;
    }
}

if (menuItem && megaMenu) {
    menuItem.forEach((item, index) => {
        const angleDown = document.createElement("i");
        const megaMenu = item.querySelector(".mega-menu");

        if (megaMenu) {
            const megaMenuHeight = megaMenu.offsetHeight;

            window.addEventListener("resize", () => { addIconOnMobile(angleDown, item) })
            window.addEventListener("load", () => {
                addIconOnMobile(angleDown, item);
                if (window.innerWidth < mobileLimit) megaMenu.style.height = `0px`;
            })

            if (window.innerWidth < mobileLimit && item.classList.contains("has-sub-menu")) {
                item.addEventListener("click", (e) => {
                    closeMega(index, e, megaMenu, megaMenuHeight)
                })
            }
        }
    })
}

// Mobile menu
window.addEventListener("DOMContentLoaded", () => {
    // Header nav bg-overlay
    const overlay = document.querySelector(".mobile-menu-overlay")
    if (headerNav && menuBtn && windowWidth < mobileLimit) {
        menuBtn.addEventListener("click", () => {
            menuBtn.classList.toggle("active");
            headerNav.classList.toggle("active");
            header.classList.toggle("mobile-menu-active");
            body.classList.toggle("overflow-hidden");
            overlay.style.height = body.offsetHeight - headerNav.offsetHeight - header.offsetHeight + "px"
            overlay.style.top = header.offsetHeight + headerNav.offsetHeight + "px"

            // headerNav.style.height = `${navHeight}px`;
            // if (!headerNav.classList.contains("active")) headerNav.style.height = "0px";

            // Mobil menü kapandığında altındaki mega menüler de kapansın.
            if (!menuBtn.classList.contains("active")) {
                menuItem.forEach(item => {
                    if (item.classList.contains("has-sub-menu")) {
                        item.classList.remove("dropdown-active")
                        item.querySelector(".mega-menu").style.height = "0px"
                        document.getElementById("header-nav").classList.remove("any-item-open")
                    }
                })
            }
        })
    }

    overlay.addEventListener("click", () => {
        menuBtn.classList.remove("active");
        headerNav.classList.remove("active");
        header.classList.remove("mobile-menu-active");
        body.classList.remove("overflow-hidden");
    })
})

// Kullanıcı sayfayı yeniden boyutlandırdığında tüm mobil menü işlemleri kapansın.
window.addEventListener("resize", () => {
    if (window.innerWidth > mobileLimit) {
        menuBtn.classList.remove("active");
        headerNav.classList.remove("active");
        header.classList.remove("mobile-menu-active");
        body.classList.remove("overflow-hidden");
        headerNav.style.height = `100%`;
    }
})

window.addEventListener("DOMContentLoaded", () => {
    /* Mobil menü kapatma */
    // menuBtn.classList.remove("active");
    // headerNav.classList.remove("active");
    // header.classList.remove("mobile-menu-active");
    // body.classList.remove("overflow-hidden");

    const subMenuItem = header.querySelectorAll(".submenu-item");

    // Mega menüde herhangi bir alt elemana hover yapıldığında resim değişsin
    if (subMenuItem) {
        const megaMenuImage = document.querySelectorAll(".mega-menu-image");

        megaMenuImage.forEach(image => {
            subMenuItem.forEach(item => {
                const itemImage = item.getAttribute("data-item-image"),
                    imageId = image.getAttribute("data-image-id"),
                    itemId = item.getAttribute("data-item-id");

                item.addEventListener("mouseenter", () => {
                    // Animasyon changed class'ı olduğunda çalışıyor.
                    if (itemId === imageId) {
                        image.classList.add("changed")

                        // 500ms sonra changed class'ı gidiyor ve animasyon tamamlanıyor.
                        setTimeout(() => {
                            image.classList.remove("changed")
                            image.setAttribute("src", itemImage);
                        },500)
                    }
                })

                const scrollableLink = item.querySelector("a")

                // Eğer hakkımızda sayfasında mega menüdeki hakkımızda sayfasına yönlendiren linklere tıklanırsa yönlendirme işlemi çalışmadan ilgili section'a yönlendirsin.
                if (scrollableLink.classList.contains("scrollable-selector") && document.querySelector(".corporate-page")) {
                    scrollableLink.addEventListener("click", (e) => {
                        if (e.target === scrollableLink || scrollableLink.contains(e.target)) e.preventDefault();
                    })
                }

                // Corporate page sayfasında menüden sayfa içi herhangi linke tıklandığında mega menü kapansın
                item.addEventListener("click", (e) => {
                    if (document.querySelector(".corporate-page")) {
                        const megaMenu = item.closest(".mega-menu")
                        megaMenu.style.display = "none"
                        menuBtn.classList.remove("active");
                        headerNav.classList.remove("active");
                        header.classList.remove("mobile-menu-active");
                        body.classList.remove("overflow-hidden");

                        setTimeout(() => {
                            megaMenu.style.display = "unset"
                            megaMenu.style.height = "0px"
                        }, 200)

                        if (windowWidth > mobileLimit) {
                            setTimeout(() => {
                                megaMenu.removeAttribute("style")
                            }, 400)
                        }
                    }

                    sessionStorage.setItem("accordion", item.getAttribute("data-tab-id"))
                })

                if (scrollableLink.classList.contains("scrollable-selector") && !document.querySelector(".corporate-page")) {
                    scrollableLink.removeAttribute("data-scrollable-section")
                    scrollableLink.classList.remove("scrollable-selector")
                }
            })
        })
    }
})