if (document.querySelector('.main-slider')) {
    let mainSlider = new Swiper(".main-slider", {
        modules: [A, P, Px],
        slidesPerView: 1,
        speed: 1000,
        loop: true,
        resistance: true,
        resistanceRatio: 0,
        watchSlidesProgress: true,
        parallax: true,
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
        },
        pagination: {
            el: ".category-pagination",
            clickable: true,
            type: "bullets",
            bulletActiveClass: 'active',
            renderBullet: function (index, className) {
                return `<div class="${className} w-[153px] xl:w-[120px] h-max flex flex-col-reverse bg-transparent rounded-none opacity-100 relative !m-0 group before:absolute before:w-full before:h-[3px] before:bg-paragraph/15 before:left-0 before:bottom-0 after:absolute after:z-2 after:left-0 after:bottom-0 after:duration-1000 after:w-0 after:h-[3px] after:bg-secondary-main [&.active]:after:w-full [&.active]:after:duration-[5000ms] first:[&.first-item]:after:w-0">
                                <span class="text-[16px] xl:text-[15px] sm:text-[14px] xs:text-[12px] xsl:leading-[16px] leading-[32px] font-light text-[#777574] duration-[5000ms] group-[&.active]:text-secondary-main block mb-[6px]"></span>
                            </div>`
            }
        },
        on: {
            init: function () {
                // Sayfa açıldığında ilk pagination item'ın after değeri active class'ından ötürü full geliyordu.
                // Bundan dolayı swiper yüklendiğinde ilk iteme first-item class'ı atayıp 'first:[&.first-item]:after:w-0' bu özelliği vererek w-0 olmasını sağlıyoruz.
                // Aşağıdaki fonkisyona göre de 1 saniye sonra first-item class'ını kaldırıyoruz
                const pagination = document.querySelectorAll(".main-slider .category-pagination .swiper-pagination-bullet")
                pagination.forEach((item, index) => {
                    if (index === 0) {
                        item.classList.add("first-item")
                        setTimeout(() => {
                            item.classList.remove("first-item")
                        }, 1000)
                    }
                })
            },
            slideChange: function () {},
        }
    })
}

if (document.querySelector('.sector-slider')) {
    window.addEventListener("DOMContentLoaded", () => {
        function scrollToSlider() { gsap.to(window, { scrollTo: { y: document.querySelector('.sector-slider').parentElement, offsetY: 150, }}) }
        const paginationItem = document.querySelectorAll(".sector-pagination .swiper-pagination-bullet"),
            navButtonPrev = document.querySelector(".pagination-navigation .sector-prev"),
            navButtonNext = document.querySelector(".pagination-navigation .sector-next");

        paginationItem.forEach(item => { item.addEventListener("click", scrollToSlider) })
        navButtonPrev.addEventListener("click", scrollToSlider)
        navButtonNext.addEventListener("click", scrollToSlider)
    })

    function scrollBarName(slide) {
        const currentSlideIndex = slide.realIndex,
            currentSlide = slide.slides[currentSlideIndex],
            slideLength = slide.slides.length,
            scrollName = document.getElementById("scrollbar-name"),
            scrollNameWrapper = scrollName.parentElement,
            lengthStart = 1,
            lengthEnd = slideLength - 2;

        scrollName.innerText = currentSlide.getAttribute("data-slide-name")

        // Eğer son slide'lara gelinmiş ise taşmama problemi için yazılar sağa hizalı olsun
        if (currentSlideIndex <= lengthStart) {
            scrollNameWrapper.classList.remove("items-center")
            // Eğer ilk slide'larda ise taşmama problemi için yazılar sola hizalı olsun
        } else if (currentSlideIndex >= lengthEnd ) {
            scrollNameWrapper.classList.remove("items-center")
            scrollNameWrapper.classList.add("items-end")
        } else {
            scrollNameWrapper.classList.add("items-center")
            scrollNameWrapper.classList.remove("items-end")
        }
    }

    let sectorSlider = new Swiper(".sector-slider", {
        modules: [A, P, N, Px, EC, S],
        slidesPerView: 1,
        speed: 1000,
        spaceBetween: 105,
        loop: true,
        resistance: true,
        resistanceRatio: 0,
        watchSlidesProgress: true,
        parallax: true,
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
        },
        navigation: {
            nextEl: ".sector-next",
            prevEl: ".sector-prev",
            disabledClass: "sector-disabled"
        },
        scrollbar: {
            el: '.swiper-scrollbar',
            hide: false,
        },
        pagination: {
            el: ".sector-pagination",
            clickable: true,
            type: "bullets",
            bulletActiveClass: 'active',
            renderBullet: function (index, className) {
                return `<div class="${className} max-w-[153px] h-auto xsm:h-[30px] w-full flex-shrink flex flex-col-reverse xsm:justify-center bg-transparent rounded-none opacity-100 relative !m-0 group before:absolute before:w-full before:h-[3px] before:bg-paragraph/15 before:md:bg-white/15 before:left-0 before:bottom-0 xs:before:bottom-[unset] after:absolute after:z-2 after:left-0 after:bottom-0 xs:after:bottom-[unset] after:duration-1000 after:w-0 after:h-[3px] after:bg-secondary-main md:after:bg-white [&.active]:after:w-full [&.active]:after:duration-[5000ms] first:[&.first-item]:after:w-0">
                                <span class="text-[16px] leading-[32px] text-[#888]/90 md:text-white/50 duration-1000 group-[&.active]:text-secondary-main group-[&.active]:md:text-white block mb-[6px] xsm:hidden"></span>
                            </div>`
            }
        },
        on: {
            init: function () {
                // Sayfa açıldığında ilk pagination item'ın after değeri active class'ından ötürü full geliyordu.
                // Bundan dolayı swiper yüklendiğinde ilk iteme first-item class'ı atayıp 'first:[&.first-item]:after:w-0' bu özelliği vererek w-0 olmasını sağlıyoruz.
                // Aşağıdaki fonkisyona göre de 1 saniye sonra first-item class'ını kaldırıyoruz
                const pagination = document.querySelectorAll(".sector-slider-area .sector-pagination .swiper-pagination-bullet")
                pagination.forEach((item, index) => {
                    if (index === 0) {
                        item.classList.add("first-item")
                        setTimeout(() => {
                            item.classList.remove("first-item")
                        }, 1000)
                    }
                })
                scrollBarName(this)
            },
            slideChange: function () {
                scrollBarName(this)
            },
        }
    })
}

if (document.querySelector('.project-slider')) {
    function changeLogo(slide) {
        const currentSlideIndex = slide.realIndex,
            currentSlide = slide.slides[currentSlideIndex],
            logos = document.querySelectorAll(".logo-area .logo"),
            groupName = document.getElementById("group-name")

        groupName.classList.add("changed")
        setTimeout(() => {
            groupName.innerText = currentSlide.getAttribute("data-slide-name")
            groupName.classList.remove("changed")
        },500)


        logos.forEach(logo => {
            logo.classList.remove("active")
            const logoID = logo.getAttribute("data-logo-id"),
                slideID = currentSlide.getAttribute("data-slide-id");
            if (slideID === logoID) {
                logo.classList.add("active");
            }
        })
    }

    let projectSlider = new Swiper(".project-slider", {
        modules: [N, Px],
        slidesPerView: 1,
        speed: 1000,
        spaceBetween: 30,
        loop: false,
        resistance: true,
        resistanceRatio: 0,
        watchSlidesProgress: true,
        parallax: true,
        allowTouchMove: false,
        navigation: {
            nextEl: ".project-next",
            prevEl: ".project-prev",
            disabledClass: "project-disabled"
        },
        on: {
            slideChange: function () {
                changeLogo(this)
            },

            init: function () {
                changeLogo(this)
            },
        }
    })

    // Logoya tıklandığında slide değişsin
    const logos = document.querySelectorAll(".logo-area .logo");
    logos.forEach(logo => {
        logo.onclick = () => {
            const logoID = parseInt(logo.getAttribute("data-logo-id"));
            projectSlider.slideTo(logoID)
        }
    })
}

